<template lang="html">
  <div class="">
    Home
    <button type="button" @click.prevent="test" name="button">test</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
    };
  },
  computed: {},
  ready() {},
  attached() {},
  methods: {
    test (){
      const postData = {
        grant_type: 'password',
        client_id: 2,
        client_secret: '013jVdhECB7mqTJubu4skWB2FrEF5v2qdTZYQYqn',
        username: 'wambakevin7@gmail.com',
        password: 'password',
        scope: ''
      }
      this.$http.post('http://localhost:8000/oauth/token',postData).then(response => {
        console.log(response);
        const header = {
          'Accept': 'application/json',
          'Authorization': response.body.token_type +' '+ response.body.access_token
        }
        this.$http.get('http://localhost:8000/api/user',{headers: header}).then(data => {
          console.log(data);
        })
      })
    }
  },
  components: {}
};
</script>

